.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;
    padding: 20px;
    grid-gap: 20px 10px;
    grid-template-areas: "h h" "n c";
    ;


    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
}

.app-wrapper-content {
    grid-area: c;
    background-color: cornflowerblue;
    border-radius: 20px;
}

